.select {
    width: 100%;
    background-color: var(--color-dark-grey);
    border: 1px solid var(--color-grey);
    border-radius: 0.5rem;
    padding: 1rem;
    color: var(--color-white);
    transition: border-color 0.2s ease;
    &:focus {
        border-color: var(--color-blue);
    }
}
